import { useBlockProps } from '@wordpress/block-editor';
import { BlockControls } from "@wordpress/block-editor";
import { MediaPlaceholder } from "@wordpress/block-editor";
import { BlockIcon } from "@wordpress/block-editor";
import { __ } from '@wordpress/i18n';
import './style.scss';
import { SelectControl } from "@wordpress/components";
import { InspectorControls } from "@wordpress/block-editor";
import { PanelBody } from "@wordpress/components";
import { ToolbarButton, ToolbarGroup } from "@wordpress/components";
import { MediaUpload, MediaUploadCheck } from "@wordpress/block-editor";
export default function Edit(props) {
	const hasImages = props.attributes.images.length > 0;
	return (
		<>
			<div {...useBlockProps()}>
				{hasImages && (
					<figure className="team-gallery-inner-container">
						{props.attributes.images.map((image, index) => (
							<img key={index} src={image.url} />
						))}
					</figure>
				)}
				{!hasImages && (
					<MediaPlaceholder
						multiple
						gallery
						icon={<BlockIcon icon="format-gallery" />}
						labels={{
							title: "Team Gallery",
							instructions: "Create an awesome team gallery.",
						}}
						onSelect={(newImages) => props.setAttributes({ images: newImages })}
					/>
				)}
			</div>
			<BlockControls>
				<ToolbarGroup>
					<MediaUploadCheck>
						<MediaUpload
							multiple
							gallery
							addToGallery={true}
							onSelect={(newImages) =>
								props.setAttributes({ images: newImages })}
							allowedTypes={["image"]}
							value={props.attributes.images.map((image) => image.id)}
							render={({ open }) => (
								<ToolbarButton onClick={open}>
									{__("Edit team pictures", "team-gallery")}
								</ToolbarButton>)}
						/>
					</MediaUploadCheck>
				</ToolbarGroup>
			</BlockControls>
			<InspectorControls>
				<PanelBody title={__("General", "team-gallery")} initialOpen>
					<SelectControl
						value={props.attributes.direction}
						options={[
							{ value: "right", label: "Right" },
							{ value: "left", label: "Left" },
						]}
						label={__("Direction", "team-gallery")}
						onChange={(newDirection) => props.setAttributes({ direction: newDirection })}
					/>
				</PanelBody>
			</InspectorControls>
		</>
	);
}